import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'
import { Link, Redirect, useLocation } from 'react-router-dom'
import Jumbo from "../components/Jumbo"
import DoomCatCard from '../components/DoomCatCard'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'

function useQuery() {
  const [query, setQuery] = useState()
  const location = useLocation()

  useEffect(() => {
    setQuery(new URLSearchParams(location.search))
  }, [location])

  return query
}

const Pagination = ({ currentPage, catsPerPage, rescuedCount }) => {
  const location = useLocation()

  return (
    <div className="flex justify-around text-purple text-sm text-center">
      <div>{currentPage.gt(1) && <Link to={`${location.pathname}?page=${currentPage.sub(1).toString()}`}>Prev Page</Link>}</div>
      <div>{currentPage.mul(catsPerPage).lt(rescuedCount) && <Link to={`${location.pathname}?page=${currentPage.add(1).toString()}`}>Next Page</Link>}</div>
    </div>
  )
}

const Ascended = () => {
  const { totalCats, remainingCats, genesisCats } = useData()
  const { account } = useWeb3()

  const query = useQuery()
  const location = useLocation()

  const [catsPerPage] = useState(BigNumber.from(16))
  const [currentPage, setCurrentPage] = useState(BigNumber.from(1))
  useEffect(() => {
    if (!query) return

    const queryPage = query.get("page") || "1"
    if (!isNaN(queryPage)) {
      setCurrentPage(BigNumber.from(queryPage))
    } else {
      setCurrentPage(BigNumber.from(0))
    }
  }, [query])

  const [rescuedCount, setRescuedCount] = useState(BigNumber.from(0))
  useEffect(() => {
    if (!remainingCats || !totalCats || !genesisCats) return
    setRescuedCount(totalCats.sub(remainingCats).sub(genesisCats))
  }, [genesisCats, remainingCats, totalCats])

  const [catIds, setCatIds] = useState([])
  useEffect(() => {
    if (!genesisCats || currentPage.eq(0)) return
    setCatIds([...new Array(catsPerPage.toNumber()).keys()].reverse().map(catIndex => {
      return BigNumber.from(catIndex).add(genesisCats).add(1).add(rescuedCount.sub(catsPerPage)).sub(catsPerPage.mul(currentPage.sub(1)))
    }).filter(e => e.gt(genesisCats)))
  }, [genesisCats, catsPerPage, rescuedCount, currentPage])

  if (currentPage.mul(catsPerPage).sub(catsPerPage).gte(rescuedCount) || currentPage.eq(0)) {
    return <Redirect to={location.pathname} />
  } else {
    return (
      <Jumbo>
        <div className="mb-2 text-center text-xl italic">{rescuedCount.toString()} DoomCats have been Rescued</div>
        {account &&
          <div className="text-center text-purple">
            <Link to="/gallery">View My DoomCats</Link>
          </div>
        }
        <div className="mt-12">
          <Pagination currentPage={currentPage} catsPerPage={catsPerPage} rescuedCount={rescuedCount} />
        </div>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-10 my-8">
          {catIds.length > 0 && catIds.map(catId => {
            return (
              <DoomCatCard tokenId={catId} key={catId.toNumber()} />
            )
          })}
        </div>
        <div>
          <Pagination currentPage={currentPage} catsPerPage={catsPerPage} rescuedCount={rescuedCount} />
        </div>
        
      </Jumbo>
    )
  }
}

export default Ascended
