import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'

const OpenSeaMessage = ({ accountCompare }) => {
  const { openSeaApproved, myCatBalance } = useData()
  const { account } = useWeb3()

  const [showOpenSeaMessage, setShowOpenSeaMessage] = useState(false)
  useEffect(() => {
    setShowOpenSeaMessage(
      account &&
      account.toLowerCase() === accountCompare.toLowerCase() &&
      myCatBalance.gt(0) &&
      !openSeaApproved
    )
  }, [account, accountCompare, myCatBalance, openSeaApproved])

  if (!showOpenSeaMessage) {
    return null
  }

  return (
    <div className="not-italic text-xs mt-2">
      <div>Make sure you've <Link to="/open-sea-approval" className="text-purple">approved your proxy contract</Link> if you plan on selling on OpenSea.</div>
    </div>
  )
}

export default OpenSeaMessage
